"use client";

import { useEffect, useState } from 'react';
import axios from 'axios';
import { Button, Card, Input } from '@shadcn/ui';

const ICMPProbes = () => {
  const [probes, setProbes] = useState([]);
  const [name, setName] = useState('');
  const [location, setLocation] = useState('');
  const [measurement, setMeasurement] = useState('icmp_probes');
  const [probeNameToUpdate, setProbeNameToUpdate] = useState('');
  const [newLocation, setNewLocation] = useState('');
  const [probeNameToDelete, setProbeNameToDelete] = useState('');

  useEffect(() => {
    const fetchProbes = async () => {
      try {
        const response = await axios.get(`${process.env.NEXT_PUBLIC_API_URL}/api/v1/icmp/list`);
        setProbes(response.data.probes);
      } catch (error) {
        console.error('Error fetching probes:', error);
      }
    };

    fetchProbes();
  }, []);

  const createProbe = async () => {
    try {
      const response = await axios.post(`${process.env.NEXT_PUBLIC_API_URL}/api/v1/icmp/create`, {
        name,
        location,
        measurement,
      });
      setProbes([...probes, response.data]);
    } catch (error) {
      console.error('Error creating probe:', error);
    }
  };

  const updateProbe = async () => {
    try {
      const response = await axios.post(`${process.env.NEXT_PUBLIC_API_URL}/api/v1/icmp/update`, {
        probe_name: probeNameToUpdate,
        location: newLocation,
      });
      setProbes(probes.map(probe => probe.name === probeNameToUpdate ? { ...probe, location: newLocation } : probe));
    } catch (error) {
      console.error('Error updating probe:', error);
    }
  };

  const deleteProbe = async () => {
    try {
      await axios.post(`${process.env.NEXT_PUBLIC_API_URL}/api/v1/icmp/delete`, {
        name: probeNameToDelete,
      });
      setProbes(probes.filter(probe => probe.name !== probeNameToDelete));
    } catch (error) {
      console.error('Error deleting probe:', error);
    }
  };

  return (
    <div className="container mx-auto p-4">
      <h1 className="text-2xl font-bold mb-4">ICMP Probes</h1>
      <div className="mb-4">
        <h2 className="text-xl font-semibold">Create Probe</h2>
        <Input placeholder="Name" value={name} onChange={(e) => setName(e.target.value)} />
        <Input placeholder="Location" value={location} onChange={(e) => setLocation(e.target.value)} />
        <Button onClick={createProbe}>Create Probe</Button>
      </div>
      <div className="mb-4">
        <h2 className="text-xl font-semibold">Update Probe</h2>
        <Input placeholder="Probe Name" value={probeNameToUpdate} onChange={(e) => setProbeNameToUpdate(e.target.value)} />
        <Input placeholder="New Location" value={newLocation} onChange={(e) => setNewLocation(e.target.value)} />
        <Button onClick={updateProbe}>Update Probe</Button>
      </div>
      <div className="mb-4">
        <h2 className="text-xl font-semibold">Delete Probe</h2>
        <Input placeholder="Probe Name" value={probeNameToDelete} onChange={(e) => setProbeNameToDelete(e.target.value)} />
        <Button onClick={deleteProbe}>Delete Probe</Button>
      </div>
      <ul>
        {probes.map((probe) => (
          <Card key={probe.id} className="mb-2">
            <p>{probe.name} - {probe.location}</p>
          </Card>
        ))}
      </ul>
    </div>
  );
};

export default ICMPProbes;